<template>
    <div class="ex-dialog">
        <jc-header :title="''" :left-options="{showBack:true}" :right-options="{showMore:true}"></jc-header>
        <div class="page-toast-container">
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="dialogAlert">Alert</jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="dialogNoTitle">Alert</jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="dialogConfirm"></jc-button>
            </div>
            <!--<div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="dialogOthers"></jc-button>
            </div>-->
            <!--<jc-dialog :value="show" :overlay="true" title="">
              <div>aaa</div>
            </jc-dialog>-->
        </div>
    </div>
</template>

<script>
    export default {
        name: 'ExDialog',
        data() {
            return {
                show: false
            };
        },
        created() {
          
        },
        methods: {
          dialogAlert() {
            this.$dialog.alert({
              title:'Alert',
              message:''
              
            })
          },
          dialogNoTitle() {
            this.$dialog.alert({
              message:''
            })
          },
          dialogConfirm() {
            this.$dialog.confirm({
              message:''
            })
            .then(res=>{
              alert('');
            })
          },
          dialogOthers() {
            this.show = true
          }
        }
    };
</script>
