<template>
    <div class="ex-toast">
        <jc-header :title="''" :left-options="{showBack:true}" :right-options="{showMore:true}"></jc-header>
        <div class="page-toast-container">
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="toastMsg"></jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="toastPositionTop"></jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="toastPositionBottom"></jc-button>
            </div>
            <div style="padding-top: 10px">
                <jc-button :type="'primary'" :full="true" @click.native="toastClick"></jc-button>
            </div>
            <div style="padding-top: 10px; display:flex; justify-content: space-around;" >
              <jc-button :type="'primary'" @click.native="toastLoading"></jc-button>
              <jc-button :type="'primary'" @click.native="toastClose"></jc-button>
            </div>
        </div>
    </div>
</template>

<script>

    export default {
        name: 'ExToast',
        data() {
            return {
                msg:'',
                show:false
            };
        },
        created(){
          
        },
        computed:{
          style() {
            return {
              'display':this.show
            }
          }
        },
        methods: {
          toastMsg() {
            this.$toast('')
          },
          toastClick() {
            this.$toast({
              message:'',
              forbidClick: true
            })
          },
          toastLoading() {
            this.$toast.loading({
              forbidClick:false
            })
          },
          
          toastClose() {
            this.$toast.clear()
          },
          toastPositionTop() {
            this.$toast({
              position:'top',
              message: ''
            })
          },
          toastPositionBottom() {
            this.$toast({
              position:'bottom',
              message: ''
            })
          }
        }
    };
</script>
<style>
.page-toast-container {
        padding: 0 20px;
        top: 50%;
        position: absolute;
        width: 100%;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
    }
    .hover {
        width:100vw;
        height:100vh;
        display:flex;
        flex-flow: column;
        justify-content: flex-end;
        background: rgba(0, 0, 0, 0.7);
        z-index: 9999;
    }
</style>